const ShapesCache = PIXI.animate.ShapesCache;

/**
 * The object returned by the ShapesTask.
 * @class Shapes
 * @memberof module:jibo/rendering/animation
 * @param {String} id ID for the shapes.
 * @param {Array<Array>} shapes Collection of shapes.
 */
class Shapes
{
    private id:string;

    constructor(id:string, shapes:any)
    {
        ShapesCache.add(id, shapes);
        this.id = id;
    }

    /**
     * Destroys shapes object.
     * @method module:jibo/rendering/animation.Shapes#destroy
     */
    destroy()
    {
        ShapesCache.remove(this.id);
    }
}

export default Shapes;
